// Project Details Page Styles

.project {
    &__hero {
        padding: 32px;

        @include tablet {
            padding: 32px 64px;
        }

        @include laptop {
            padding: 56px 128px;
        }
    }

    & p {
        margin-bottom: 24px;
    }

    &__wrapper {
        margin: 0 auto;
    }

    &__section {
        padding: 32px 32px 40px;
        width: 100vw;

        @include tablet {
            padding: 32px 64px 48px;
        }

        @include laptop {
            padding: 80px 128px;
        }
    }

    &__section-content {
        max-width: $tablet-width;
        margin: 0 auto;
    }

    &__title {
        margin-bottom: 24px;

        @include tablet {
            font-size: 40px;
            line-height: 80px;
        }
    }

    &__subtitle {
        color: $dark-gray;
    }

    &__discipline{
        color: $gray;
        margin-bottom: 32px;

        @include laptop {
            font-size: 24px;
            margin-bottom: 48px;
        }

        @include small-desktop {
            margin-bottom: 72px;
        }
    }

    &__overview {
        margin-bottom: 40px;

        &--left {
            margin-bottom: 24px;
        }

        &--right {
            
            & li {
                justify-content: flex-start;
                align-items: flex-end;
                margin-bottom: 24px;
            }

            & img {
                width: 32px;
                height: auto;
                margin-right: 8px;
            }

            & p {
                margin: 0;
            }
        }
    }

    &__ctas {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 16px;
    }

    &__ctas a {
        margin-bottom: 24px;
    }

    &__img {
        width: 100%;
        height: auto;
        @extend .box-shadow;

        @include small-desktop {
            height: auto;
            width: 768px;
        }
    }

    &__nav {
        margin-bottom: 80px;
        display: block;
    }

    &__nav-wrap {
        position: relative;
        width: fit-content;
        &::before {
            content: '';
            background-color: $primary-light;
            position: absolute;
            left: 0;
            bottom: -6px;
            width: 100%;
            height: 6px;
            z-index: -1;
            transition: all .3s ease-in-out;
        }
    
        &:hover::before {
            bottom: 0;
            height: 100%;
        }
    }

    &__nav span {
        margin-left: 8px;
    }

}
